interface IOptions {
  duration: number;
  progress: (e: number) => void;
  done: (e: number) => void;
}

export default class Animation {
  private timer = 0;

  private startTime = 0;

  private readonly duration: number;

  private readonly progress: (e: number) => void;

  private readonly done: (e: number) => void;

  constructor(options: IOptions) {
    this.duration = options.duration;
    this.progress = options.progress;
    this.done = options.done;
  }

  private readonly handleProgress = (): void => {
    const p = (Date.now() - this.startTime) / this.duration;
    if (p >= 1) {
      this.done(1);
    } else {
      this.progress(p);
      this.timer = requestAnimationFrame(this.handleProgress);
    }
  };

  start(): Animation {
    this.clear();
    this.startTime = Date.now();
    this.timer = requestAnimationFrame(this.handleProgress);
    return this;
  }

  clear(): Animation {
    if (this.timer) {
      cancelAnimationFrame(this.timer);
      this.timer = 0;
    }
    return this;
  }
}
